
.header-nav {
  list-style: none;
  padding-left: 0;
  margin: 0;
  line-height: 30px;
  height: 30px;
}
.header-nav .nav-item {
  float: left;
  margin-right: 20px;
}
.header-nav .nav-item >a, .header-nav .nav-item >div {
  color: #666;
  border-bottom: 3px solid transparent;
  display: block;
}
.header-nav .nav-item >a.router-link-active, .header-nav .nav-item >div.router-link-active {
  border-bottom-color: #42b983;
  color: #333;
}
.header-nav .nav-item >a:hover, .header-nav .nav-item >div:hover {
  color: #333;
}
.header-nav .nav-item .arrow {
  display: inline-block;
  vertical-align: middle;
  margin-top: -1px;
  margin-left: 6px;
  width: 0;
  height: 0;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-top: 5px solid #ccc;
}
.header-nav .nav-item-dropdown {
  position: relative;
}
.header-nav .nav-item-dropdown .dropdown-list {
  position: absolute;
  top: 100%;
  left: 50%;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
  display: none;
  background-color: white;
  margin: 0;
  list-style-type: none;
  border: 1px solid #e2e2e2;
  border-radius: 3px;
  white-space: nowrap;
  padding: 10px 0;
}
.header-nav .nav-item-dropdown .dropdown-list .dropdown-item {
  padding: 0 20px;
}
.header-nav .nav-item-dropdown .dropdown-list .dropdown-item .router-link:hover, .header-nav .nav-item-dropdown .dropdown-list .dropdown-item .router-link-active {
  color: #42b983;
}
.header-nav .nav-item-dropdown:hover .dropdown-list {
  display: block;
}
.header-nav.is-mobile {
  height: auto;
  line-height: auto;
}
.header-nav.is-mobile .nav-item {
  float: none;
}
.header-nav.is-mobile .nav-item >a, .header-nav.is-mobile .nav-item >div {
  border-bottom: none;
}
.header-nav.is-mobile .nav-item >a.router-link-active, .header-nav.is-mobile .nav-item >div.router-link-active {
  color: #42b983;
}
.header-nav.is-mobile .dropdown-list {
  background-color: transparent;
}
.header-nav.is-mobile .nav-item-dropdown .dropdown-list {
  position: initial;
  display: block;
  -webkit-transform: none;
  -ms-transform: none;
  transform: none;
  border: none;
  padding: 0;
}



