
.inner-x {
  padding: 0 20px;
}
.inner-y {
  padding: 20px 0;
}
.custom-components {
  padding: 30px;
}
@media screen and (max-width: 768px) {
.inner-x, .custom-components {
    padding: 10px;
}
.inner-y {
    padding: 10px 0;
}
}
[aria-label] {
  text-rendering: optimizeSpeed;
}
[router-link], [jump-to-id] {
  cursor: pointer;
}



